import {API} from "../components/API/Api";

const TOGGLE_FOLLOW = 'TOGGLE-FOLLOW'
const SET_USERS = 'SET_USERS'
const SET_ACTIVE_PAGE = 'SET_ACTIVE_PAGE'
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT'
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING'

let initialState = {
    users: [],
    pageSize: 3,
    totalUsersCount: 0,
    activePage: 1,
    isFetching: true,
    followingInProgress: []
}


const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case TOGGLE_FOLLOW:
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: !u.followed}
                    }

                    return u;
                })
            }
        case SET_USERS: {
            return {...state, users: action.users}
        }
        case SET_ACTIVE_PAGE: {
            return {...state, activePage: action.activePage}
        }
        case SET_TOTAL_USERS_COUNT: {
            return {...state, totalUsersCount: action.totalCount}
        }
        case TOGGLE_IS_FETCHING: {
            return {...state, isFetching: action.isFetching}
        }
        case 'TOGGLE_FOLLOWING_IN_PROGRESS': {
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id != action.userId)
            }
        }
        default:
            return state;


    }
}

export const toggleFollow = (userId) => ({type: 'TOGGLE-FOLLOW', userId});
export const setUsers = (users) => ({type: 'SET_USERS', users});
export const setActivePage = (activePage) => ({type: 'SET_ACTIVE_PAGE', activePage});
export const setTotalUsersCount = (totalUsersCount) => ({type: 'SET_TOTAL_USERS_COUNT', totalCount: totalUsersCount});
export const toggleIsFetching = (isFetching) => ({type: 'TOGGLE_IS_FETCHING', isFetching})
export const toggleFollowingInProgress = (isFetching, userId) => ({
    type: 'TOGGLE_FOLLOWING_IN_PROGRESS',
    isFetching,
    userId
})

export const getUsers = (activePage, pageSize, isAuth) => async (dispatch) => {
    dispatch(toggleIsFetching(true))
    let data = await API.getUsers(activePage, pageSize)
    dispatch(toggleIsFetching(false));
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(data.totalCount));

}

const followUnfollow = async (dispatch, userId, actionCreator, apiMethod) => {

    dispatch(toggleFollowingInProgress(true, userId))
    let response = await apiMethod(userId)

    if (response.data.resultCode === 0) {
        dispatch(actionCreator(userId));
    }
    dispatch(toggleFollowingInProgress(false, userId))

}

export const unfollowThunk = (userId) => async (dispatch) => {

    followUnfollow(dispatch, userId, toggleFollow, API.unfollow.bind(userId));
}

export const followThunk = (userId) => async (dispatch) => {

    followUnfollow(dispatch, userId, toggleFollow, API.follow.bind(userId));
}


export default usersReducer;
