import React from "react";
import {Formik, Field, Form} from 'formik';
import {loginFormAPI} from "../components/API/Api";
import {connect} from "react-redux";
import {loginThunk} from "../redux/authReducer";
import {Redirect} from "react-router-dom";
import * as yup from "yup";
import s from "./Login.module.css"


 const LoginForm = (props) => {

     const validationSchema = yup.object().shape({
         email: yup.string().email().typeError('Введите вашу почту').required('Введите вашу почту'),
     })


     if (props.isAuth) {
         return <Redirect to={'/profile'}/>
     }

     return <>
         <h1 className={s.loginArcticle}>Войдите в свой аккаунт</h1>
     <Formik
         initialValues={{
             email: '',
             password: '',
             rememberMe: false,
             captcha: false
         }}
         onSubmit={async (values) => {
             loginFormAPI.authLogin(values).then(props.loginThunk())
             validationSchema = {validationSchema}


         }}
     >
         {({values, errors, touched}) => (
             <Form>
                 <div className={s.inputGroup1}>
                     <Field name="email" placeholder="Введите вашу почту" type="email"/>
                     <label htmlFor="email">Email</label>
                     {errors.email && touched.email ?
                         <div>{errors.email}</div> : null}
                     <div className={s.reqMark1}></div>
                     <br></br>
                 </div>

                 <div className={s.inputGroup1}>
                     <Field name="password" placeholder="Введите вашу почту" type="password"/>
                     <label htmlFor="password">Password</label>
                     <div className={s.reqMark1}></div>
                     <br></br>
                 </div>
                 <label htmlFor="rememberMe">
                     <Field type="checkbox" name="rememberMe"/>
                     Запомнить меня
                 </label>
                 <br></br>
                 <button className={s.login} type={"submit"}> Залогиниться</button>

             </Form>
         )}
     </Formik>
 </>
 }

let mapStateToProps = (state) => {
    return {
        isAuth: state.auth.isAuth
    }

}


export default connect(mapStateToProps, {loginThunk})(LoginForm)
