import {API, loginFormAPI} from "../components/API/Api";


const SET_USER_DATA = 'authReducer/SET_USER_DATA';
const SET_USER_DATA_AFTER_RENDER = 'authReducer/SET_USER_DATA_AFTER_RENDER';


let initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: null

}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA: {
            return {
                ...state,
                ...action.data
            };
        }
        case SET_USER_DATA_AFTER_RENDER: {
            return {
                ...state,
                isAuth: action.data
            };
        }
        default:
            return state;

    }
}

export const setAuthUserData = (login, email, id, isAuth) => ({type: SET_USER_DATA, data: {login, email, id, isAuth}});
export const setAuthUserDataAfterRender = (isAuth) => ({type: SET_USER_DATA_AFTER_RENDER, data: isAuth});

export const loginThunk = () => async (dispatch) => {
    let response = await API.header();
        if (response.data.resultCode === 0) {
            let {login, email, id, isAuth} = response.data.data;
            dispatch(setAuthUserData(login, email, id, true));}

}


export const logoutThunk = () => async  (dispatch) => {

   let response = await loginFormAPI.authLogout()

            if (response.data.resultCode === 0) {
                dispatch(setAuthUserData(null, null, null, false));}

}

export default authReducer;