import * as axios from "axios";

const instance = axios.create({
    withCredentials: true,
    baseURL: `https://social-network.samuraijs.com/api/1.0/`,
    headers: {
        'API-KEY': 'a192ecc9-3a69-47da-b1c4-eb1137d2209a'
    }
});
const instanceNoCred = axios.create({
    baseURL: `https://social-network.samuraijs.com/api/1.0/`,
    headers: {
        'API-KEY': 'a192ecc9-3a69-47da-b1c4-eb1137d2209a'
    }
});

export const API = {
    getUsers(activePage, pageSize) {
        return instance.get(`users?page=${activePage}&count=${pageSize}`)
            .then(response => {
                return response.data
            })

    },
    follow(id) {
        return instance.post(`follow/${id}`).then(n => {
            return n
        })
    },
    unfollow(id) {
        return instance.delete(`follow/${id}`).then(n => {
            return n
        })

    },
    getFriend(friend, pageSize) {
        return instance.get(`users?friend=${friend}&count=${pageSize}`)
            .then(response => {
                return response.data
            })

    },
    header() {
        return instance.get(`auth/me`).then(n => {
            return n
        })
    },
    profile(userId) {
        return profileAPI.profile(userId)
    }
}

export const profileAPI = {

    profile(userId) {
        return instance.get(`profile/` + userId).then(n => {
            return n
        })
    },
    profileStatus(statusText) {
        return instance.put(`profile/status/`, {status: statusText})
    },
    getStatus(userId) {
        return instance.get(`profile/status/` + userId)
    },
    saveAvatar(file) {
        const formData = new FormData();
        formData.append('image', file);
        return instance.put(`profile/photo`, formData, {headers: {'Content-Type': 'multipart/form-data'}}
        );},
    changeProfileData(values){
        return instance.put(`profile`, values)
    }
}


export const loginFormAPI = {
    authLogin(values){
        return instance.post(`auth/login`, {email: values.email, password: values.password, rememberMe: values.rememberMe, captcha: false})
    },
    authLogout(){
        return instance.delete(`auth/login`)
    }

}