const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            return {
                ...state,
                posts: [{postId: 3, likesCount: 0, message: action.postText}, ...state.posts]
            };
        }
            ;
        case
        SET_USER_PAGE: {
            return {...state, userPage: action.userPage};
        }
            ;
        case DELETE_POST: {
            return {
                ...state,
                posts: state.posts.filter(p => p.postId !== action.userId)
            };
        }
        case
        SET_STATUS_TEXT: {
            return {
                ...state,
                statusText: action.statusText
            };
        }
        case
        SAVE_PHOTO_SUCCESS: {
            return {
                ...state,
                userPage: {...state.userPage, photos: action.photos}
            };
        }

        default:
            return state;

    }
}


export const addPostActionCreator = (text) => ({type: ADD_POST, postText: text});
export const deletePost = (userId) => ({type: DELETE_POST, userId});
export const setUserProfile = (userPage) => ({type: 'SET_USER_PAGE', userPage: userPage});
export const setStatus = (statusText) => ({type: 'SET_STATUS_TEXT', statusText});
export const savePhotoSuccess = (photos) => ({type: 'SAVE_PHOTO_SUCCESS', photos});

export const profileThunk = (userId) => async (dispatch) => {
    let response = await   API.profile(userId)
            dispatch(setUserProfile(response.data));

}

export const getStatusThunk = (userId) => async (dispatch) => {
      const response = await  profileAPI.getStatus(userId);
            dispatch(setStatus(response.data))

}
export const updateStatusThunk = (statusText) => async (dispatch) => {
    let response = await profileAPI.profileStatus(statusText)
    if (response.data.resultCode === 0) {
        dispatch(setStatus(statusText))
    }
}

export const saveAvatar = (file) => async (dispatch) => {
    let response = await profileAPI.saveAvatar(file)
    if (response.data.resultCode === 0) {
        dispatch(savePhotoSuccess(response.data.data.photos))
    }
}

export const saveProfile = (values) => async (dispatch, getState) => {
   const userId = getState().auth.id
    let response = await profileAPI.changeProfileData(values)
    if (response.data.resultCode === 0) {
        dispatch(profileThunk(userId))
    }
}


