const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case "ADD-MESSAGE" :{

            return{
                ...state,
                messages: [...state.messages, {id:'i', message: action.text}]
            }
    }
        default:
            let stateCopy = {...state}
            return stateCopy
    }

}


export const addMessageCreator = (text) =>({type:'ADD-MESSAGE', text});


export default dialogsReducer