import React from "react";
import s from '../Header/Header.module.css'
import {NavLink} from "react-router-dom";



const Header = (props) => {

    return (<>


        <header className={s.header}>
             <img src='https://smajlik.ru/wp-content/uploads/2017/12/2-1024x816.png'/>

            {props.isAuth ? <div className={s.login1}> <p className={s.nickname}>{props.login}</p> <button className={s.logout} onClick={props.logoutThunk}> выйти </button></div> :  <NavLink to={'/login'} className={s.login} >
                Войти
            </NavLink>  }

        </header>
</>
    );

}
export default Header;
