import {loginFormAPI} from "../components/API/Api";

const GET_USER_PROPERTIES = 'GET_USER_PROPERTIES'

let initialState = {
    userProp: null
}


const loginReducer = (state = initialState, action) => {
    switch (action.type) {
        case GET_USER_PROPERTIES :
            return {
                ...state,
                userProp: action.userProperty.login

            }
        default:
            return state
    }
}

export const getUserProperties = (formData) => ({type: GET_USER_PROPERTIES, userProperty: {...formData}})

export const loginFormThunk = (userProp) => {
    return (dispatch) => {
        dispatch(getUserProperties(userProp))
        loginFormAPI.authLogin(userProp)
    }
}


        export default loginReducer;